/* SCROLL BAR */
::-webkit-scrollbar {
  width: 10px;
}
::-webkit-scrollbar-track {
  background-color: rgba(0, 0, 0, 1);
}
::-webkit-scrollbar-thumb {
  background: #F7838C;
}

/* UNIVERSAL STYLING */

*{
  scroll-behavior: smooth;
  box-sizing: border-box;
  text-decoration: none;
}
html{
  margin: 0;
  padding: 0;
}
body{
    font-family:'Times New Roman', Times, serif;
    background-color: #FFFFFF;
    font-size: 16px;
    margin: 0;
    padding: 0;
}
button, a{
  cursor: pointer;
}

/* GENERAL CLASSES */
#content-container{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav{
  color: rgba(0,0,0);
}
.nav:hover{
  text-decoration: underline;
}

.err{
  background-color: red;
  color: white;
  width: 100%;
  margin-bottom: -20px !important;
  text-align: center;
}

.center{
  display: flex;
  justify-content: center;
}

.hide{
  display: none;
}

.end{
  padding-bottom: 10px;
  border-bottom: 1px solid black;
  width: 100%;
}

@media only screen and (max-width: 271px){
  *{
      width: 100% !important;
  }
}