#statusReviewPage{
    height: 100vh;
    max-width: 800px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

#success{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
#success i{
    font-size: 20vh;
    color: green;
    border: 10px solid green;
    padding: 10px;
    border-radius: 100%;
}

#error{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
#error i{
    font-size: 20vh;
    color: red;
    border: 10px solid RED;
    padding: 10px;
    
}

.msg{
    border: 1px solid rgba(0,0,0,0.5);
    padding: 10px;
    width: 80%;
    text-align: center;
}
