@import url('https://fonts.googleapis.com/css2?family=Coustard:wght@400;900&family=League+Gothic&family=Roboto:wght@400;700&display=swap');

#reviewPage{
    max-width: 800px;
    display: flex;
    justify-content: center;
    align-items: center;
}

#reviewContent{
    font-family: "Coustard", serif;
    font-size: 2vh;
    width: 100%;
    padding: 5%;
}

#reviewContent h1{
    font-family: "League Gothic", sans-serif;
    font-weight: bold;
    font-size: 7vh;
    margin: 2vh 0;
}

input{
    height: 5vh;
}
textarea{
    height: 20vh;
}
input, textarea{
    border: 1px solid rgba(0,0,0,0.5);
}

input:hover, textarea:hover{
    border: 1px solid rgba(0,0,0,1);
}

#formReview{
    display: flex;
    flex-direction: column;
}
#formReview *{
    margin: 10px 0;
    font-size: 2vh;
    padding: 20px;
}

/* RATING */
.rating {
    display: inline-block;
    opacity: 1;
    margin: 0 !important;
    padding: 0 !important;
    width: 100%;
  }
  
  .rating input {
    display: none;
    opacity: 1;
    width: 100%;
  }
  
  .rating label {
    float: right;
    cursor: pointer;
    color: #ccc;
    transition: color 0.3s, transform 0.3s, box-shadow 0.3s;
    text-align: center;
    width: 20%;
  }
  
  .rating label:before {
    content: '\2605';
    font-size: 30px;
    transition: color 0.3s;
  }
  
  .rating input:checked ~ label,
  .rating label:hover,
  .rating label:hover ~ label {
    color: #ffc300;
    transform: scale(1.2);
    transition: color 0.3s, transform 0.3s, box-shadow 0.3s;
    animation: bounce 0.5s ease-in-out alternate;
  }
  
  @keyframes bounce {
    to {
      transform: scale(1.3);
    }
  }

  /* UPLOADER */
  .image-uploader {
    text-align: center;
    margin: 0 !important;
    padding: 0!important;
  }
  
  .custom-file-upload {
    border: 1px solid rgba(0,0,0,0.5);
    display: inline-block;
    padding: 6px 12px;
    cursor: pointer;
    border-radius: 4px;
    background-color: transparent;
    color: #333;
    font-style: italic;
  }
  .custom-file-upload:hover{
    background-color: rgba(0,0,0,0.05);
  }
  
  .selected-images {
    margin-top: 20px;
  }
  
  .selected-images img {
    width: 100px;
    height: 100px;
    margin: 5px;
    object-fit: cover;
  }
  

/* LOADER */
.loader {
    position: relative;
    width: 2.5em;
    height: 2.5em;
    transform: rotate(165deg);
   }
   
   .loader:before, .loader:after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    display: block;
    width: 0.5em;
    height: 0.5em;
    border-radius: 0.25em;
    transform: translate(-50%, -50%);
   }
   
   .loader:before {
    animation: before8 2s infinite;
   }
   
   .loader:after {
    animation: after6 2s infinite;
   }
   
   @keyframes before8 {
    0% {
     width: 0.5em;
     box-shadow: 1em -0.5em rgba(225, 20, 98, 0.75), -1em 0.5em rgba(111, 202, 220, 0.75);
    }
   
    35% {
     width: 2.5em;
     box-shadow: 0 -0.5em rgba(225, 20, 98, 0.75), 0 0.5em rgba(111, 202, 220, 0.75);
    }
   
    70% {
     width: 0.5em;
     box-shadow: -1em -0.5em rgba(225, 20, 98, 0.75), 1em 0.5em rgba(111, 202, 220, 0.75);
    }
   
    100% {
     box-shadow: 1em -0.5em rgba(225, 20, 98, 0.75), -1em 0.5em rgba(111, 202, 220, 0.75);
    }
   }
   
   @keyframes after6 {
    0% {
     height: 0.5em;
     box-shadow: 0.5em 1em rgba(61, 184, 143, 0.75), -0.5em -1em rgba(233, 169, 32, 0.75);
    }
   
    35% {
     height: 2.5em;
     box-shadow: 0.5em 0 rgba(61, 184, 143, 0.75), -0.5em 0 rgba(233, 169, 32, 0.75);
    }
   
    70% {
     height: 0.5em;
     box-shadow: 0.5em -1em rgba(61, 184, 143, 0.75), -0.5em 1em rgba(233, 169, 32, 0.75);
    }
   
    100% {
     box-shadow: 0.5em 1em rgba(61, 184, 143, 0.75), -0.5em -1em rgba(233, 169, 32, 0.75);
    }
   }
   
   .loader {
    position: absolute;
    top: calc(50% - 1.25em);
    left: calc(50% - 1.25em);
   }