/* FONTS */
@import url('https://fonts.googleapis.com/css2?family=Coustard:wght@400;900&family=League+Gothic&family=Roboto:wght@400;700&display=swap');

#homePage {
    max-width: 900px;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
}

#arcoHome {
    margin: 0;
    padding: 0;
    width: 100%;
}

#arcoHome img {
    width: 100%;
    min-height: 100px;
    object-fit: cover;
}

/* Títulos */
#homePage h2 {
    text-align: center;
    font-weight: bold;
    font-size: 6vh;
    font-family: "League Gothic", sans-serif;
    font-weight: 400;
    font-style: normal;
}

#homePage h3 {
    text-align: center;
    font-size: 3vh;
    font-family: "Coustard", serif;
    font-weight: 400;
    font-style: normal;
}

#hello {
    padding: 0 10%;
    text-align: center;
}

/* Navegación */
#homeNav {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 10%;
}

#homeNav .homeBtn {
    background-color: #F7838C;
    color: white;
    padding: 16px;
    margin: 15px 0;
    border-radius: 50px;
    width: 80%;
    font-size: 1.8vh;
    font-family: "Coustard", serif;
    font-weight: 400;
    text-align: center;
    transition: background-color 0.3s ease;
}

#homeNav .homeBtn:hover {
    background-color: #f78a93;
}

/* Ajustes para pantallas más grandes */
@media (min-width: 768px) {
    #homePage h2 {
        font-size: 5vw;
    }

    #homePage h3 {
        font-size: 2.5vw;
    }

    #homeNav .homeBtn {
        width: 60%;
        padding: 20px;
        font-size: 2vw;
    }
}

/* Ajustes para pantallas pequeñas (celulares) */
@media (max-width: 767px) {
    #homePage {
        padding: 10px;
    }

    #arcoHome img {
        min-height: 80px;
    }

    #homePage h2 {
        font-size: 5vh;
    }

    #homePage h3 {
        font-size: 2.5vh;
    }

/* Ajustes para pantallas pequeñas (celulares) */
@media (max-width: 767px) {
    #homeNav .homeBtn {
        width: 90%; 
        padding: 18px;
        font-size: 2.5vh; 
    }
}
    #homeNav {
        margin-bottom: 15%;
    }
}

/* Ajustes para pantallas muy pequeñas */
@media (max-width: 480px) {
    #homePage h2 {
        font-size: 4vh;
    }

    #homePage h3 {
        font-size: 2vh;
    }

    #homeNav .homeBtn {
        width: 100%;
        padding: 20px;
        font-size: 3vh;
    }
}
