/* FONTS */
@import url('https://fonts.googleapis.com/css2?family=Work+Sans:wght@400;600&display=swap');

/* Global Styles */
body {
    margin: 0;
    padding: 0;
    font-family: 'Work Sans', sans-serif;
    background-color: #f9f9f9;
    color: #333;
}

/* Section Styles */
#reviewPage {
    max-width: 900px;
    margin: 20px auto;
    padding: 20px;
    background-color: #fff;
    border-radius: 12px;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
}

#reviewContent {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

h1 {
    font-size: 2rem;
    color: #444;
    margin-bottom: 20px;
    font-weight: 600;
    text-align: center;
}

/* Form Styles */
#formReview {
    width: 100%;
}

label {
    font-weight: 600;
    color: #555;
    margin-bottom: 5px;
    display: block;
    font-size: 1rem;
}

input, select {
    width: 100%;
    padding: 12px;
    margin-bottom: 20px;
    border: 1px solid #ddd;
    border-radius: 8px;
    font-size: 1rem;
    color: #333;
    background-color: #fafafa;
    transition: border-color 0.3s ease;
}

input:focus, select:focus {
    border-color: #999;
    outline: none;
}

.hide {
    display: none;
}

/* Image Upload */
.image-uploader {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
}

.custom-file-upload {
    display: inline-block;
    padding: 10px 20px;
    cursor: pointer;
    background-color: #999;
    color: white;
    border-radius: 8px;
    transition: background-color 0.3s ease;
}

.custom-file-upload:hover {
    background-color: #777;
}

.image-uploader img {
    margin-top: 15px;
    max-width: 100%;
    border-radius: 8px;
}

/* Button */
#homeNav {
    display: flex;
    justify-content: center;
}

.homeBtn {
    background-color: #555;
    color: white;
    padding: 15px 30px;
    border: none;
    border-radius: 50px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease;
}

.homeBtn:hover {
    background-color: #333;
    transform: translateY(-2px);
}

.err {
    color: red;
    margin-top: 10px;
    font-size: 0.9rem;
    text-align: center;
}

/* Responsive Design */
@media (max-width: 768px) {
    #reviewPage {
        padding: 15px;
    }

    h1 {
        font-size: 1.8rem;
    }

    input, select {
        font-size: 0.9rem;
        padding: 10px;
    }

    .homeBtn {
        padding: 12px 20px;
        width: 90%;
        font-size: 0.9rem;
    }
}

@media (max-width: 480px) {
    h1 {
        font-size: 1.5rem;
    }

    #reviewPage {
        max-width: 95%;
        margin: 10px auto;
    }

    input, select {
        font-size: 0.85rem;
        padding: 8px;
    }

    .homeBtn {
        padding: 10px 15px;
        width: 100%;
        font-size: 0.85rem;
    }
}
